.approach {
  padding-top: 80px;
  background-color: #f7f8f8;
  @include small-tablet {
    padding: 80px 0;
  }
  @include mobile {
    padding: 0 0 var(--offset);
  }
  &__container {
    @include tablet {
      display: block;
    }
  }
  &__top {
    background: #F7F8F8;
  }
  &__left {
    grid-column: 5 span;
    padding-top: 33px;
    @include tablet {
      padding-bottom: var(--offset);
    }
  }
  &__title {
    margin: 0;
    margin-bottom: 13px;
    font-weight: 400;
    font-size: 22px;
    line-height: 22px;
    color: var(--dark-text);
    @include tablet {
      text-align: center;
    }
    @include mobile {
      text-align: left;
    }
  }
  &__descr {
    margin: 0;
    margin-bottom: 37px;
    font-size: 15px;
    line-height: 24px;
    color: rgba(53, 83, 98, 0.8);
  }
  &__btn {
    border-color: rgba(53, 83, 98, 0.2);
    color: #00413E;
  }
  &__btn.focus-visible {
    border-color: var(--active-color);
  }
  &__right {
    grid-column: 7 / 6 span;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    padding-bottom: 104px;
    @include mobile {
      padding-bottom: var(--offset);
    }
    @include tablet {
      justify-content: center;
      flex-wrap: wrap;
      gap: var(--offset);
    }
    @include small-tablet {
      padding-bottom: var(--offset);
    }
  }
  &__img {
    @include mobile {
      display: block;
      width: 100%;
      height: auto;
    }
  }
  &__bottom {
    padding-top: 24px;
  }
  &__list {
    padding-bottom: 21px;
    @include mobile {
      display: block;
    }
  }
  &__item {
    grid-column: 2 span;
    @include tablet {
      grid-column: 4 span;
    }
    @include mobile {
      grid-column: 12 span;
    }
  }
  &__link {
    display: block;
    border-top: 1px solid rgba(53, 83, 98, 0.3);
    padding-top: 16px;
    padding-left: 32px;
    font-size: 15px;
    line-height: 24px;
    background-repeat: no-repeat;
    background-size: 24px 24px;
    background-position: left top 16px;
    color: rgba(53, 83, 98, 0.8);
    transition: color .3s ease-in-out;
    @include mobile {
      padding-bottom: 16px;
    }
    &--ted {
      background-image: url("../img/ted_2x.png");
    }
    &--nyt {
      background-image: url("../img/nyt_2x.png");
    }
    &--scoop {
      background-image: url("../img/scoop_2x.png");
    }
    &--ap {
      background-image: url("../img/ap_2x.png");
    }
    &--time {
      background-image: url("../img/time_2x.png");
    }
    &--freethink {
      background-image: url("../img/freethink_2x.png");
    }
    &:focus {
      outline: none;
    }
    @include hover {
      color: var(--accent-color);
    }
    &:active {
      color: var(--active-color);
    }
  }
  &__link.focus-visible {
    outline: 1px solid var(--active-color);
    outline-offset: 3px;
  }
}
