.information {
  padding: 88px 0 140px;
  background: #F4F5F5;
  @include mobile {
    padding: var(--offset) 0;
  }
  &__content {
    @include small-tablet {
      display: block;
    }
  }
  &__img {
    width: 100%;
    @include mobile {
      display: block;
      height: auto;
    }
  }
  &__title {
    grid-column: 6 span;
    margin: 0;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 31px;
    line-height: 38px;
    letter-spacing: 1.2px;
    color: #003331;
    @include small-tablet {
      margin-bottom: var(--offset);
    }
    @include mobile {
      font-size: 28px;
    }
  }
  &__list {
    grid-column: 7 / 6 span;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 32px;
    @include small-tablet {
      grid-column: 1 / 12 span;
    }
    @include mobile {
      display: block;
    }
  }
  &__item {
    grid-column: 3 span;
    @include mobile {
      margin-bottom: var(--offset);
    }
  }
  &__subtitle {
    margin: 0;
    margin-bottom: 4px;
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    color: #003331;
  }
  &__descr {
    margin: 0;
    font-size: 15px;
    line-height: 24px;
    color: rgba(53, 83, 98, 0.8);
  }
}
