    .benefits {
      padding: 120px 0;
      background-color: var(--light-color);
      @include mobile {
        padding: var(--offset) 0 0;
      }
      &__title {
        margin: 0 auto 57px;
        max-width: 717px;
        font-family: 'LibreBaskervilleRegular';
        font-weight: 400;
        font-size: 30px;
        line-height: 38px;
        text-align: center;
        letter-spacing: 1.2px;
        color: var(--dark-text);
        @include mobile {
          font-size: 24px;
        }
      }
      &__list {
        counter-reset: partners;
        justify-content: space-between;
        margin: 0 auto;
        max-width: 758px;
        @include mobile {
          display: block;
        }
      }
      &__item {
        display: flex;
        flex-direction: column;
        align-items: center;
        @include mobile {
          margin-bottom: var(--offset);
        }
        &::before {
          display: flex;
          justify-content: center;
          align-items: center;
          counter-increment: partners;
          content: counter(partners);
          margin-bottom: 19px;
          width: 48px;
          height: 48px;
          left: 305px;
          top: 1067px;
          background: #014C48;
          border-radius: 999px;
          font-size: 16px;
          line-height: 30px;
          text-align: center;
          color: var(--light-color);
        }
        &--line {
          position: relative;
          flex-grow: 1;
          @include mobile {
            display: none;
          }
          &::before {
            display: none;
          }
          &::after {
            content: "";
            position: absolute;
            left: 0;
            top: 50%;
            margin-top: -35px;
            width: 100%;
            height: 1px;
            background: rgba(53, 83, 98, 0.2);
            transform: translateY(-50%);
          }
        }
      }
      &__descr {
        margin: 0;
        width: 142px;
        font-size: 15px;
        line-height: 24px;
        text-align: center;
        color: rgba(53, 83, 98, 0.8);
      }
    }
