.partners {
  padding: 16px 0;
  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 6px;
    @include mid-desktop {
      flex-direction: column;
    }
  }
  &__title {
    margin: 0;
    font-weight: 400;
    font-size: 11px;
    line-height: 16px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: rgba(53, 83, 98, 0.6);
  }
  &__list {
    display: flex;
    justify-content: space-between;
    gap: 12px;
    @include mid-desktop {
      flex-wrap: wrap;
      justify-content: center;
    }
  }
  &__link {
    display: block;
    // width: 160px;
    // height: 67px;
  }
  &__link--seeall {
    font-size: 11px;
    line-height: 16px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: rgba(53, 83, 98, 0.6);
    &:focus {
      outline: none;
    }
    @include hover {
      color: var(--accent-color);
    }
    &:active {
      color: var(--active-color);
    }
  }
  &__link.focus-visible {
    outline: 1px solid var(--active-color);
    outline-offset: 3px;
  }
}
