.where {
  padding: 140px 0;
  background-color: #001F1F;
  @include tablet {
    padding: 70px 0;
  }
  @include mobile {
    padding: var(--offset) 0;
  }
  &__container {
    @include tablet {
      display: block;
    }
  }
  &__content {
    grid-column: 6 span;
    @include tablet {
      margin-bottom: var(--offset);
    }
  }
  &__text {
    width: 57%;
    @include tablet {
      width: 100%;
      text-align: center;
    }
  }
  &__title {
    margin: 0;
    margin-bottom: 43px;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 36px;
    line-height: 52px;
    letter-spacing: 1.2px;
    color: #FAFAFA;
  }
  &__descr {
    margin: 0;
    margin-bottom: 37px;
    font-weight: 400;
    font-size: 18px;
    line-height: 28px;
    color: #FAFAFA;
  }
  &__link {
    border: none;
    background: #00665F;
  }
  &__link.focus-visible {
    outline: 1px solid var(--active-color);
    outline-offset: 3px;
  }
  &__img {
    grid-column: 7 / 6 span;
    @include tablet {
      text-align: center;
    }
    & img {
      @include mobile {
        display: block;
        width: 100%;
        height: auto;
      }
    }
  }
}
