.footer {
  &__container {
    @include mobile {
      display: flex;
      flex-wrap: wrap;
    }
  }
  &__top {
    padding: 112px 0;
    background-color: #001414;
    @include mobile {
      padding: var(--offset) 0;
    }
  }
  &__nav {
    grid-column: 2 span;
    @include small-tablet {
      grid-column: 3 span;
    }
    @include mobile {
      grid-column: 6 span;
    }
    &--network {
      grid-column: 11 / 2 span;
      @include small-tablet {
        grid-column: 10 / 3 span;
      }
      @include mobile {
        grid-column: 7 / 6 span;
      }
    }
  }
  &__subtitle {
    position: relative;
    margin: 0;
    margin-bottom: 23px;
    padding-bottom: 24px;
    font-weight: 400;
    font-size: 11px;
    line-height: 16px;
    letter-spacing: 1px;
    text-transform: uppercase;
    color: #7D9798;
    &::after {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 16px;
      height: 1px;
      background-color: #25B895;
    }
  }
  &__link {
    font-size: 15px;
    line-height: 30px;
    color: #EDECEB;
    transition: color .3s ease-in-out;
    &:focus {
      outline: none;
    }
    @include hover {
      color: var(--accent-color);
    }
    &:active {
      color: var(--active-color);
    }
  }
  &__link.focus-visible {
    outline: 1px solid var(--active-color);
    outline-offset: 3px;
  }
  &__bottom {
    padding: 45px 0;
    background: #000f0f;
  }
  &__inner {
    justify-content: space-between;
    align-items: center;
    @include mobile {
      flex-direction: column;
      gap: var(--offset);
    }
  }
  &__img {
    width: 24px;
    height: 24px;
  }
  &__text {
    margin: 0;
    font-size: 9px;
    line-height: 10px;
    text-align: right;
    color: #E5ECEC;
    @include small-tablet {
      font-size: 12px;
      line-height: 18px;
      text-align: center;
    }
  }
}

.copyright-list {
  display: flex;
  justify-content: flex-end;
  gap: 8px;
  @include mobile {
    flex-direction: column;
    align-items: center;
    gap: 16px;
    margin-bottom: var(--offset);
  }
  &__link {
    font-size: 10px;
    line-height: 10px;
    color: #E5ECEC;
    transition: color .3s ease-in-out;
    @include mobile {
      font-size: 16px;
    }
    &:focus {
      outline: none;
    }
    @include hover {
      color: var(--accent-color);
    }
    &:active {
      color: var(--active-color);
    }
  }
  &__link.focus-visible {
    outline: 1px solid var(--active-color);
    outline-offset: 3px;
  }
}
