.hero {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-top: 189px;
  min-height: 776px;
  background-repeat: no-repeat;
  background-image: url("../img/hero-bg.jpg");
  background-size: cover;
  @include tablet {
    background-position: 40% top;
  }
  @include small-tablet {
    padding-top: 150px;
    background-position: 50% top;
  }
  @include mobile {
    background-position: 60% top;
  }
  &__container {
    @include small-tablet {
      display: block;
    }
  }
  .container--bottom {
    width: 100%;
  }
  &__content {
    grid-column: 6 span;
    margin-bottom: var(--offset);
  }
  &__title {
    margin: 0;
    margin-bottom: 29px;
    font-family: var(--second-family);
    font-size: 38px;
    font-weight: 400;
    line-height: 44px;
    letter-spacing: 1.2px;
    color: var(--light-color);
    @include small-tablet {
      letter-spacing: 0;
      text-align: center;
    }
    @include mobile {
      font-size: 28px;
    }
  }
  &__descr {
    margin: 0;
    margin-bottom: 29px;
    font-size: 18px;
    line-height: 28px;
    color: var(--light-color);
  }
}

.states {
  &__title {
    margin: 0;
    margin-bottom: 15px;
    font-weight: 400;
    font-size: 15px;
    line-height: 24px;
    color: var(--light-color);
  }
  &__list {
    padding-bottom: 40px;
    @include mobile {
      display: block;
    }
  }
  &__item {
    grid-column: 2 span;
    @include small-tablet {
      grid-column: 4 span;
    }
  }
  &__link {
    display: block;
    border-top: 1px solid rgba(255, 255, 255, 0.6);
    padding-top: 17px;
    font-size: 15px;
    line-height: 24px;
    color: var(--light-color);
    transition: color .3s ease-in-out;
    @include mobile {
      padding-bottom: 17px;
    }
    &:focus {
      outline: none;
    }
    @include hover {
      color: var(--accent-color);
    }
    &:active {
      color: var(--active-color);
    }
  }
  &__link.focus-visible {
    outline: 1px solid var(--active-color);
    outline-offset: 3px;
  }
}
