.info {
  display: flex;
  height: 680px;
  background: #001414;
  @include tablet {
    padding: 100px 0;
    height: 100%;
  }
  &::before {
    content: "";
    flex-shrink: 0;
    width: 287px;
    height: 100%;
    background-image: url("../img/info-1.jpg");
    @include tablet {
      display: none;
    }
  }
  &::after {
    content: "";
    flex-shrink: 0;
    width: 287px;
    height: 100%;
    background-image: url("../img/info-2.jpg");
    @include tablet {
      display: none;
    }
  }
  &__inner {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-right: 1px solid #003331;
    border-left: 1px solid #003331;
    @include tablet {
      border-right: none;
      border-left: none;
    }
  }
  &__content {
    padding: 0 24%;
    @include tablet {
      padding: 0 10%;
    }
  }
  &__title {
    margin: 0;
    margin-bottom: 21px;
    font-size: 22px;
    font-weight: 400;
    line-height: 22px;
    color: var(--light-color);
  }
  &__descr {
    margin: 0;
    font-size: 15px;
    line-height: 24px;
    color: var(--light-color);
  }
}
