.press {
  padding: 87px 0;
  @include mobile {
    padding: var(--offset) 0;
  }
  &__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-bottom: 24px;
    @include mobile {
      flex-direction: column;
      gap: var(--offset);
      margin-bottom: var(--offset);
    }
  }
  &__title {
    margin: 0;
    font-family: var(--second-family);
    font-weight: 400;
    font-size: 27px;
    line-height: 38px;
    letter-spacing: 1.2px;
    color: var(--dark-text);
  }
  &__link {
    border: 1px solid rgba(53, 83, 98, 0.2);
    color: #00413E;
  }
  &__list {
    @include mobile {
      display: block;
    }
  }
  &__item {
    grid-column: 4 span;
    border-top: 1px solid rgba(53, 83, 98, 0.3);
    padding: 16px 0 50px;
    @include small-tablet {
      grid-column: 6 span;
    }
    @include mobile {}
  }
  &__meta {
    margin-bottom: 7px;
    padding-left: 40px;
    min-height: 24px;
  }
}

.meta-press {
  background-repeat: no-repeat;
  background-position: 0 0;
  background-size: 24px;
  &__title {
    display: inline-block;
    margin-right: 16px;
    font-size: 11px;
    line-height: 16px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: rgba(53, 83, 98, 0.6);
  }
  &__date {
    font-size: 11px;
    line-height: 16px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    color: rgba(53, 83, 98, 0.6);
  }
  &__link {
    display: block;
    font-weight: 400;
    font-size: 17px;
    line-height: 25px;
    color: #00413E;
    transition: color .3s ease-in-out;
    &:focus {
      outline: none;
    }
    @include hover {
      color: var(--accent-color);
    }
    &:active {
      color: var(--active-color);
    }
  }
  &__link.focus-visible {
    outline: 1px solid var(--active-color);
    outline-offset: 3px;
  }
  &--tedtalks {
    background-image: url("../img/ted_2x.png");
  }
  &--correctionalnews {
    background-image: url("../img/correctional-news-logo.png");
  }
  &--departmentofjustice {
    background-image: url("../img/department-of-justice-logo.png");
  }
  &--nyt {
    background-image: url("../img/nyt_2x.png");
  }
  &--corrections1 {
    background-image: url("../img/corrections1-logo.png");
  }
  &--rightoncrime {
    background-image: url("../img/right-on-crimes-logo.png");
  }
}
