.header {
  padding-top: 17px;
  &--position {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }
  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__navigation {
    display: flex;
    align-items: center;
    &.menu--active {
      visibility: visible;
      transform: none;
    }
    @include small-tablet {
      position: fixed;
      left: 0;
      top: 0;
      flex-direction: column;
      align-items: flex-start;
      gap: 51px;
      z-index: 100;
      padding: var(--offset);
      width: 80%;
      height: 100vh;
      background-color: #000f0f;
      overflow-y: auto;
      visibility: hidden;
      transform: translateX(-100%);
      transition: transform .3s ease-in-out, visibility .3s ease-in-out;
    }
  }
  &__login {
    margin-right: 16px;
    margin-left: 32px;
    font-size: 13px;
    line-height: 16px;
    color: var(--light-color);
    transition: color .3s ease-in-out;
    @include small-tablet {
      margin: 0;
      font-size: 18px;
    }
    &:focus {
      outline: none;
    }
    @include hover {
      color: var(--accent-color);
    }
    &:active {
      color: var(--active-color);
    }
  }
  &__login.focus-visible {
    outline: 1px solid var(--active-color);
    outline-offset: 3px;
  }
}

.top-menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 51px;
  @include small-tablet {
    flex-direction: column;
    align-items: flex-start;
  }
  &__link {
    font-size: 13px;
    line-height: 16px;
    color: var(--light-color);
    transition: color .3s ease-in-out;
    @include small-tablet {
      font-size: 18px;
    }
    &:focus {
      outline: none;
    }
    @include hover {
      color: var(--accent-color);
    }
    &:active {
      color: var(--active-color);
    }
  }
  &__link.focus-visible {
    outline: 1px solid var(--active-color);
    outline-offset: 3px;
  }
}
